import React from "react";
import {Badge, Button, Offcanvas} from "react-bootstrap";
import {Phone, Whatsapp} from "react-bootstrap-icons";
import Discounts from "./Discounts";
import DiscountList from "./DiscountList";

 const MoreInfo =(props)=>{


     const darkM = props.darkM
     const value = props.value

     return (
         <div>
             <Offcanvas show={props.show} onHide={props.handleClose} placement='end'
                        style={{color:(props.darkM?'#000000':'#ffffff'),backgroundColor:(!props.darkM?'#1a1e23':'#ffffff')}}
             >
                 <Offcanvas.Header closeButton>
                     <Offcanvas.Title>Больше информации</Offcanvas.Title>
                 </Offcanvas.Header>
                 <Offcanvas.Body >
                  <h2 >{value.name}</h2>
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     {value.phone ?<>
                     <Button variant='primary' type='tel'
                             href={'tel:' + value.phone}><Phone/> {value.phone} </Button>
                     <Button className='m-3' variant='success' type='tel' href={'https://wa.me/'+value.phone.replace(/[^0-9]/g, '')} target="_blank">
                         <Whatsapp/>
                     </Button>
                         </> :null}
                     {value.address ? <Button variant={!darkM?'warning':'primary'} href={'https://yandex.ru/maps/?text=' + value.address} bg="secondary" target="_blank">{value.address}</Button>
                         :null }
                 </Offcanvas.Body>
                 <Offcanvas.Body>

                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     Рейтинг среди ресторанов : {value.ranking}
                 </Offcanvas.Body>
                 <Offcanvas.Body>
                     <div>Описание заведения:</div>
                     <div>{value.description || null}</div>

                 </Offcanvas.Body>
                 <Offcanvas.Body style={{height:'35%'}}>
                     <div>Акции заведения:</div>
                     {value.discounts? <DiscountList
                         darkM={darkM}
                         value={value.discounts}
                         writeNewState={props.writeNewState}/>:null}
                 </Offcanvas.Body>
             </Offcanvas>
         </div>
     )
 }

 export default MoreInfo