import React, {useState} from "react";
import {Button, Card, Offcanvas} from "react-bootstrap";
import MoreInfo from "./MoreInfo";
import AddDiscounts from "./AddDiscounts";

const Cafe = (props) => {



    const [loading, setLoading] = useState(false)
    const [image, setImage] = useState(false)
    const darkM = props.darkM
    const [show, setShow] = useState(false);

    const [showM, setShowM] = useState(false);

    const handleCloseM = () => setShowM(false);
    const handleShowM = () => setShowM(true);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
        <Card style={{width: '19rem' , margin:"auto"}}
              bg={darkM ? 'light' : 'dark'}
              text={!darkM ? 'white' : 'dark'}
              key={props.key}>
            <Card.Img variant="top"
                      key={props.key}
                      src={props.value.photo ? props.value.photo.images.medium.url : 'https://серебро.рф/img/placeholder.png'}
                      style={{width: '302px', height: '300px'}}/>
            <Card.Body>
                <Card.Title>{props.value.name}</Card.Title>
                <Card.Text>
                    {props.value.description}

                </Card.Text>
                <Button variant={!darkM?'warning':'primary'} onClick={handleShow}>Перейти</Button>
                {props.user?.email ?<Button style={{marginLeft: '30px'}} variant={darkM ? 'warning' : 'primary'} onClick={handleShowM}>Добавить
                    Акцию</Button>:null}
            </Card.Body>
        </Card>
            <AddDiscounts
                handleCloseM={handleCloseM}
                handleShowM={handleShowM}
                showM={showM}
                setShowM={setShowM}
                writeNewState={props.writeNewState}
                value={props.value}
                id = {props.id}


            />
            <MoreInfo
                key={props.key}
                show={show}
                handleClose={handleClose}
                handleShow={handleShow}
                value={props.value}
                darkM={darkM}
                writeNewState={props.writeNewState}

            />
        </>

    )
}

export default Cafe