import React, {useState} from "react";
import {Badge, Card, Carousel} from "react-bootstrap";

const DiscountList = (props) => {
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    const value = props.value
    return (
        <>
            <Carousel activeIndex={index} onSelect={handleSelect} variant={props.darkM ? 'dark' : null}>
                {Object.values(value).map(value =>

                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={value.img}
                            alt="First slide"
                        />

                        <Carousel.Caption>
                            <Card style={{backgroundColor:'#1c1c1cb5' , color:'#ffffff'}} >
                                <Card.Body>
                                    <Card.Title>{value.name}</Card.Title>
                                    <Card.Text>{value.description}</Card.Text>
                                    <Badge>{value.date}</Badge>
                                </Card.Body>
                            </Card>
                        </Carousel.Caption>

                    </Carousel.Item>
                )}
            </Carousel>
        </>
    )
}

export default DiscountList