import React from "react";
import Cafe from "./Cafe";
import {Col, Container, Row} from "react-bootstrap";

const CafeList=(props) =>{
    return(
        <>
            <Container fluid style={{width:'100%', marginTop: '70px'}}
            >
                <Row xl='4' className="justify-content-md-center align-content-center" >
                    {Object.keys(props.state).map((key)=> {

                      const  value = props.state[key]

                        return (value.name ?


                            <Col className='mb-2 mt-2' key={value.location_id}>
                                <Cafe darkM={props.darkM}
                                      value={props.state[key]}
                                      key={value.location_id}
                                      id={key}
                                      user={props.user}

                                writeNewState={props.writeNewState}/>
                            </Col>


                            : null)
                    }
            )}
                </Row>
                </Container>
        </>
    )
}
export default CafeList