import React, {useEffect, useState} from "react";
import Cafe from "./Components/Cafe";
import axios from "axios";
import {initializeApp} from 'firebase/app';
import {child, getDatabase, onValue, push, ref, set, update} from "firebase/database";
import Header from "./Components/Header";
import 'bootstrap/dist/css/bootstrap.min.css';
import CafeList from "./Components/CafeList";
import { getAuth, createUserWithEmailAndPassword , signOut, signInWithEmailAndPassword,onAuthStateChanged} from "firebase/auth";




const app = initializeApp({
    apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
    authDomain: "dagecafe-278e6.firebaseapp.com",
    databaseURL: "https://dagecafe-278e6-default-rtdb.europe-west1.firebasedatabase.app",
    projectId: "dagecafe-278e6",
    storageBucket: "dagecafe-278e6.appspot.com",
    messagingSenderId: "359918608297",
    appId: "1:359918608297:web:72457a6ebc0983b3afeeb8"
});



function writeData(state) {
    const db = getDatabase();
    set(ref(db, `/`), {
        state: state
    })
        .then(() => {
            console.log('записано')
        });
}

const App = () => {

    useEffect(()=>{
        readData()
    },[])


    function writeNewState(id,discount) {
        const db = getDatabase();


        // Get a key for a new Post.
        const discountKey = push(child(ref(db), `discounts`)).key;

        // Write the new post's data simultaneously in the posts list and the user's post list.
        const updates = {};
        updates[`/state/${id}/discounts/` + discountKey] = discount;


        return update(ref(db), updates);
    }

    const [stateOne, setStateOne] = useState([])
    const [state, setState] = useState([])
    const [stateTwo, setStateTwo] = useState([])
    const [stateThree, setStateThree] = useState([])
    const [stateFour, setStateFour] = useState([])
    const [maps, setMaps] = useState([
        [[43.038031, 47.515495], [42.983307, 47.390230]],
        [[42.983307, 47.543622], [42.966015, 47.397425]],
        [[42.966015, 47.585225], [42.899349, 47.506337]],
        [[42.910650, 47.645012], [42.860864, 47.594644]]
    ])
    const [darkM, setDarkM] = useState(false)
    const [email,setEmail] = useState()
    const [password,setPassword] = useState()
    const [loginEmail,setLoginEmail] = useState('')
    const [loginPassword,setLoginPassword] = useState('')
    const [user,setUser] = useState({})


    const auth = getAuth(app);


    const logIn = async () =>{
        try {
            const user = await signInWithEmailAndPassword(auth, loginEmail, loginPassword)
            console.log(user)
        }
        catch (error) {
            console.log( error.message)
        }}

    const logOut = async () =>{
        await signOut(auth)
    }




    function readData(state) {
        const db = getDatabase();
        const starCountRef = ref(db, '/' + '/state');
        onValue(starCountRef, (snapshot) => {
            const data = snapshot.val();
            setState(data);
        });
    }

    const Loader = (maps, state, setState) => {
        const options = {
            method: 'GET',
            url: 'https://travel-advisor.p.rapidapi.com/restaurants/list-in-boundary',
            params: {
                bl_latitude: `${maps[1][0]}`,
                tr_latitude: `${maps[0][0]}`,
                bl_longitude: `${maps[1][1]}`,
                tr_longitude: `${maps[0][1]}`,
                restaurant_tagcategory_standalone: '10591',
                restaurant_tagcategory: '10591',
                currency: 'RUB',
                lunit: 'km',
                lang: 'en_US'
            },
            headers: {
                'x-rapidapi-host': 'travel-advisor.p.rapidapi.com',
                'x-rapidapi-key': process.env.REACT_APP_XRAPID_API_KEY
            }
        };

        axios.request(options).then(function (response) {
            console.log(response.data);
            setState(response.data.data)
        }).catch(function (error) {
            console.error(error);
        });
    };

    const LoaderAllData = () => {
        setTimeout(Loader(maps[0], stateOne, setStateOne), 1000)
        setTimeout(Loader(maps[1], stateTwo, setStateTwo), 10000)
        setTimeout(Loader(maps[2], stateThree, setStateThree), 20000)
        setTimeout(Loader(maps[3], stateFour, setStateFour), 30000)

    }

    return (
        <div style={{ position:'absolute', backgroundColor: (!darkM ? '#000000' : '#ffffff')}}>
            <Header
                readData={readData}
                state={state}
                setDarkM={setDarkM}
                email={email}
                setEmail={setEmail}
                setPassword={setPassword}
                password={password}
                darkM={darkM}
                auth ={auth}
                logIn={logIn}
                logOut={logOut}
                setLoginEmail={setLoginEmail}
                setLoginPassword={setLoginPassword}
                login={logIn}
                setUser={setUser}
                user={user}

            />
            <CafeList key='CafeList'
                state={state}
            darkM={darkM}
                      writeNewState={writeNewState}
                      user={user}
            />
            </div>
    )
}

export default App;
