import React, {useEffect, useState} from "react";
import {Button, Form, Modal} from "react-bootstrap";

const AddDiscounts=(props)=>{

    const [discount,setDiscount] = useState({})
    const [add,setAdd] = useState(false)

    const nameRef = React.createRef()
    const imgRef = React.createRef()
    const dateRef = React.createRef()
    const descriptionRef = React.createRef()

  useEffect(()=>{
        if(add){
            props.writeNewState(props.id,discount)
            setAdd(false)
        }
    },[discount])

    const addDiscount = ()=>{
        let discoun = {
            name: nameRef.current.value,
            img: imgRef.current.value,
            date: dateRef.current.value,
            description: descriptionRef.current.value
        }
        setDiscount(discoun)
    }

    return (
        <>
            <Modal show={props.showM} onHide={props.handleCloseM}>
                <Modal.Header closeButton>
                    <Modal.Title>Добавить Акцию</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group className="mb-3" >
                            <Form.Label >Название акции</Form.Label>
                            <Form.Control ref={nameRef}  placeholder='Название акции' />
                            <Form.Label >Картинка акции</Form.Label>
                            <Form.Control ref={imgRef}  placeholder='Ссылка на картинку' />
                            <Form.Label >Сроки акции</Form.Label>
                            <Form.Control ref={dateRef}  placeholder='Сроки акции' />
                        </Form.Group>
                        <Form.Group className="mb-3" >
                            <Form.Label>Описание</Form.Label>
                            <Form.Control ref={descriptionRef} as="textarea" rows={3} />
                        </Form.Group>

                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={()=>{props.handleCloseM()
                    addDiscount()
                    setAdd(true)}} >Добавить</Button>
                </Modal.Footer>
            </Modal>

        </>
    )
}

export default AddDiscounts