import React, {useState} from "react";
import {Button, Container, Form, Image, Nav, Navbar, Offcanvas} from "react-bootstrap";
import {Moon, Sun} from 'react-bootstrap-icons';
import {onAuthStateChanged} from "firebase/auth";

const Header = (props) => {

    const [open, setOpen] = useState(false)
    const darkM = props.darkM
    const setDarkM = props.setDarkM

onAuthStateChanged(props.auth,(currentUser)=>{
        props.setUser(currentUser)
    })



    return <>
        <Navbar fixed='top'
                style={{width: '100%'}}
                expand={false}
                bg={darkM ? 'light' : 'dark'}
                variant={darkM ? 'light' : 'dark'}>
            <Container fluid>
                <Navbar.Brand>DC</Navbar.Brand>
                <Button variant={!darkM ? 'outline-light' : 'outline-dark'}
                        onClick={() => setDarkM(!darkM)}>
                    {darkM? <Sun height='30px' width='25px'/> : <Moon height='30px' width='25px'/>}

                </Button>
                <Navbar.Toggle aria-controls="offcanvasNavbar"/>

                <Navbar.Offcanvas
                    id="offcanvasNavbar"
                    aria-labelledby="offcanvasNavbarLabel"
                    placement="start"
                    style={{backgroundColor: (!props.darkM ? '#1a1e23' : '#ffffff')}}

                >
                    <Offcanvas.Header closeButton
                                      bg='dark'>
                        <Offcanvas.Title id="offcanvasNavbarLabel"
                                         style={{color: (props.darkM ? '#000000' : '#ffffff')}}>Dagestan
                            Cafes</Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body >
                        <Nav>
                            <Nav.Link>Главная</Nav.Link>
                            <Nav.Link>Горячие акций</Nav.Link>

                        </Nav>
                        <Nav className="justify-content-end bottom-0 flex-grow-1 pe-3">


                            {!props.user?.email ? <Form style={!darkM ? {color: '#ffffff'} : {color: '#000000'}}>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Control onChange={(event) => props.setLoginEmail(event.target.value)}
                                                  type="email" placeholder="Введите email"/>
                                    <Form.Text className="text-muted">
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Control onChange={(event) => props.setLoginPassword(event.target.value)}
                                                  type="password" placeholder="Введите пороль"/>
                                </Form.Group>
                                <Button variant="primary" onClick={props.logIn}>
                                    Войти
                                </Button>
                            </Form>:<Button variant="primary"  onClick={props.logOut}>
                                Выйти
                            </Button>}



                        </Nav>
                    </Offcanvas.Body>
                </Navbar.Offcanvas>
            </Container>

        </Navbar>
    </>

}

export default Header